            Пожиратели Смерти, отряд которых возглавляют Люциус Малфой и
            Беллатриса Лестрейндж, ответственная за пытки над родителями Невилла
            Долгопупса, учинённые ею 14 лет назад до основных событий.<br />
            Все понимают, что видение с Сириусом было ловушкой, но Отряд
            Дамблдора сумел прорваться через окружение и бежать. Однако
