            преследует убийцу и с помощью заклинания «Круциатус» сбивает
            Беллатрису с ног (заклинание не подействовало в полную силу, так как
            нужно иметь сильное желание причинить боль). Появляются
            Волан-де-Морт и Дамблдор. Между ними начинается поединок, в
