            готовится прикончить его, но через Летучий порох появляются министр
            Фадж и мракоборцы. Волан-де-Морт трансгрессирует, и в газете
            «Ежедневный Пророк» сообщается, что: <br />Дамблдор и Поттер
            реабилитированы;<br />
            Фадж уходит в отставку под давлением Министерства;<br />
            Амбридж отстранена от должности Генерального инспектора и Директора
            Хогвартса;<br />
            возвращение «Того, кого нельзя называть» официально подтверждено;<br />
            Дамблдор восстановлен в должности директора Хогвартса.<br />
            Альбус рассказывает правду о пророчестве, а Поттер говорит своим
            друзьям, что вера в себя и своих друзей — это ключ к борьбе с
            Волан-де-Мортом.
